import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			totalItems: 0, //总数量
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			loadedItemIds: new Set()
		};
	},
	onLoad(){
		
	},
	onShow() {
		this.loadData();
	},
	watch:{
		//显示空白页
		cartList(e){
			let empty = e.length === 0 ? true: false;
			if(this.empty !== empty){
				this.empty = empty;
			}
		}
	},
	computed:{
		...mapState(['hasLogin'])
	},
	methods: {
		//请求数据
		async loadData(){
			const that = this
			that.$api.request('cart', 'getCartList').then(res => {
				res.data.forEach(item => {
					item.checked = true
				})
				that.cartList = res.data
				that.calcTotal();  //计算总价
			})
		},
		//监听image加载完成
		onImageLoad(item) {
			this.loadedItemIds.add(item.id)
			this.$forceUpdate()
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].skuImg = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		check(type, index){
			if(type === 'item'){
				this.cartList[index].checked = !this.cartList[index].checked;
			}else{
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item=>{
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data){
			const that = this
			that.$api.request('cart','updateCartItemNum', {
				cartId: that.cartList[data.index].id,
				num: data.number
			}, failres => {
				uni.showToast({
					title: failres.errmsg,
					icon: 'none'
				});
				that.cartList[data.index].num = that.cartList[data.index].num
			}).then(res => {
				that.cartList[data.index].num = data.number;
				that.calcTotal();
			})
		},
		//删除
		deleteCartItem(index){
			const that = this
			that.$api.request('cart', 'removeCartItem', {
				cartId: that.cartList[index].id
			}).then(res => {
				that.cartList.splice(index, 1);
				that.calcTotal();
				//uni.hideLoading();
			})				
		},
		//清空
		clearCart(){
			const that = this
			uni.showModal({
				content: '清空购物车？',
				success: (e)=>{
					if(e.confirm){
						that.$api.request('cart','removeCartAll').then(res => {
							that.cartList = []
						})
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			const that = this
			let list = that.cartList;
			if(list.length === 0){
				that.empty = true;
				return;
			}
			let total = 0;
			let totalItems = 0;
			let checked = true;
			list.forEach(item=>{
				if(item.checked === true){
					totalItems += item.num
					total += (that.isVip ? item.vipPrice : item.price) * item.num;
				}else if(checked === true){
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
			this.totalItems = totalItems
		},
		//创建订单
		createOrder(){
			//滤除未被选择的item
			let selectedItems = []
			this.cartList.forEach(item => {
				if (item.checked) {
					selectedItems.push(item)
				}
			})
			if (selectedItems.length === 0) {
				this.$api.msg('您没有选中任何商品')
				return
			}
			this.$api.globalData.skuList = selectedItems
			uni.navigateTo({
				url: `/pages/order/create?takeway=cart`
			})
		}
	}
}
